import { useEffect } from 'react';

const TildaButtonTracker = () => {
  useEffect(() => {
    const button = document.querySelector('#rec994544431');
    
    if (button) {
      const handleClick = () => {
        console.log(' Tilda  !');
      };

      button.addEventListener('click', handleClick);

      //     
      return () => {
        button.removeEventListener('click', handleClick);
      };
    }
  }, []);

  return null; //      
};

export default TildaButtonTracker; 